import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController, ModalController, ToastController } from 'ionic-angular';

import { SoapService } from '../soap.service';
import { Storage } from '@ionic/storage';
import { api_base_url, api_user, api_pass } from '../../config';
import { DatePicker } from '@ionic-native/date-picker';
import { DatePipe } from '@angular/common';

/**
 * Generated class for the SurveyDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-survey-detail',
  providers: [SoapService],
  templateUrl: 'survey-detail.html',
})
export class SurveyDetailPage {
  userdataTPK: any;
  survey_header: any;
  questionList: Array<any> = [];
  isLoading: Boolean = true;
  answerList: Array<any> = [];

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public soapService: SoapService,
    public loadingCtrl: LoadingController,
    public storage: Storage,
    public alertCtrl: AlertController,
    public modalCtrl: ModalController,
    public toastCtrl: ToastController,
    public datePicker: DatePicker,
    public datePipe: DatePipe,
  ) {
    this.storage.get('userdataTPK').then(val => {
      this.userdataTPK = val;
    });

    this.survey_header = navParams.get('data');
    
  }

  ionViewDidEnter() { 
    this.getQuestionList();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SurveyDetailPage');
  }

  getQuestionList() {
    this.isLoading = true;
    this.soapService
      .post(api_base_url, 'eoffice_survey_detail', {
        fStream: JSON.stringify(
          {
            usernameEDI: api_user,
            passwordEDI: api_pass,
            id_survey: this.survey_header['ID_SURVEY']
          }
        )
      }).then(result => {
        var responData = JSON.parse(String(result));
        console.log(responData);
        if (responData['rcmsg'] == "SUCCESS") {
          if (responData['data'].length > 0) {
            for (var i = 0; i < responData['data'].length; i++) {
              this.questionList.push(responData['data'][i]);
              this.answerList.push({
                ID_QUESTION: responData['data'][i]['ID_QUESTION'],
                TYPE: responData['data'][i]['TYPE'],
                ID_OPTION: (responData['data'][i]['TYPE'] == 'LINEAR SCALE' ) ? responData['data'][i]['DATA'][0]['ID_OPTION'] : '',
                ANSWER: ''
              })
            }

            console.log(this.answerList);


          }
        } else {
          let toast = this.toastCtrl.create({
            message: 'Gagal mendapatkan data question, silahkan coba kembali.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
        }

        this.isLoading = false;

      })
      .catch(error => {
        let toast = this.toastCtrl.create({
          message: 'Gagal mendapatkan data question, periksa koneksi internet anda.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();


        this.isLoading = false;
      });
  }

  showDatePicker(questionType, index) {
    var mymode = '';
    if (questionType == 'DATETIME') {
      mymode = 'datetime';
    } else if (questionType == 'TIME') {
      mymode = 'time';
    } else if (questionType == 'DATE') {
      mymode = 'date';
    } else {
      mymode = 'datetime';
    }
    var mydate;
    this.datePicker.show({
      date: new Date(),
      mode: mymode,
      androidTheme: this.datePicker.ANDROID_THEMES.THEME_HOLO_LIGHT
    }).then(date => {
      if (questionType == 'DATETIME') {
        mydate = this.datePipe.transform(date, 'dd-MM-yyyy H:mm');
      } else if (questionType == 'TIME') {
        mydate = this.datePipe.transform(date, 'H:mm');
      } else if (questionType == 'DATE') {
        mydate = this.datePipe.transform(date, 'dd-MM-yyyy');
      } else {
        mydate = this.datePipe.transform(date, 'dd-MM-yyyy H:mm');
      }

      this.answerList[index]['ANSWER'] = mydate;
      console.log(this.answerList);
    },
      err => console.log('Error occurred while getting date: ', err)
    );
  }

  submit() {
    var error = [];
    console.log(this.answerList);

    console.log(this.questionList[1]['TYPE']);
    for (let i = 0; i < this.questionList.length; i++) {
      if (this.questionList[i]['TYPE'] == 'TEXT' || this.questionList[i]['TYPE'] == 'LINEAR SCALE' || this.questionList[i]['TYPE'] == 'DATETIME' || this.questionList[i]['TYPE'] == 'TIME' || this.questionList[i]['TYPE'] == 'DATE') {
        if (this.answerList[i]['ANSWER'] == '') {
          error.push('error ke-' + i);
        }
      }

      if (this.questionList[i]['TYPE'] == 'MULTIPLE' || this.questionList[i]['TYPE'] == 'CHECKBOX') {
        if (this.answerList[i]['ID_OPTION'] == '') {
          error.push('error ke-' + i);
        }
      }
    }

    if (error.length > 0) {
      let toast = this.toastCtrl.create({
        message: 'semua pertanyaan harus dijawab !',
        duration: 3000,
        position: 'bottom'
      });
      toast.present();
    } else {
      console.log(JSON.stringify(this.answerList));
      var finalJson = {
        usernameEDI: api_user,
        passwordEDI: api_pass,
        id_survey: this.survey_header['ID_SURVEY'],
        nipp: this.userdataTPK['data']['NIPP'],
        answer_list: this.answerList
      }
      console.log(JSON.stringify(finalJson));
      let loading = this.loadingCtrl.create({
        spinner: 'dots',
        content: "Mengirim survey...",
        cssClass: 'transparent',
        dismissOnPageChange: true
      });
      loading.present();

      this.soapService
        .post(api_base_url, 'eoffice_survey_answer', {
          fStream: JSON.stringify(
            finalJson
          )
        }).then(result => {
          var responData = JSON.parse(String(result));
          if (responData['rcmsg'] == "SUCCESS") {
            let toast = this.toastCtrl.create({
              message: 'Sukses mengirim respon survey.',
              duration: 3000,
              position: 'bottom'
            });
            toast.present();   
            this.navCtrl.pop();
          } else {
            let toast = this.toastCtrl.create({
              message: 'Gagal mengirim survey, silahkan coba kembali.',
              duration: 3000,
              position: 'bottom'
            });
            toast.present();
          }
          
          loading.present();
        })
        .catch(error => {
          let toast = this.toastCtrl.create({
            message: 'Gagal mengirim survey, periksa koneksi internet anda.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();    
          loading.present();      
        });
    }
  }  



}



